.dark-mode{
  display:flex;
  align-items:center;
  justify-content:space-between;
}
.switch{
  position:relative;
  display:inline-block;
  width:60px;
  height:34px;
}
.switch input{
  opacity:0;
  width:0;
  height:0;
}
.slider{
  position:absolute;
  cursor:pointer;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background-color:#ccc;
  -webkit-transition:.4s;
  transition:.4s;
}
.slider:before{
  position:absolute;
  content:"";
  height:26px;
  width:26px;
  left:4px;
  bottom:4px;
  background-color:#282943;
  -webkit-transition:.4s;
  transition:.4s;
}
input:checked + .slider{
  background-image:linear-gradient(to right, #4796d2 0%, #37ce8f 100%);
}
input:focus + .slider{
  box-shadow:0 0 1px #1ABC9C;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}